import './index.css';

const arrowLeft = document.querySelector('.arrow-left');
const arrowRight = document.querySelector('.arrow-right');
const slide = document.querySelector('.slide');

const data = [
  {name: 'slide one', content: 'content for slide one', active: true, img: '/images/elephants.jpeg'},
  {name: 'slide two', content: 'content for slide two', active: false, img: '/images/elephants2.jpg'},
  {name: 'slide three', content: 'content for slide three', active: false, img: '/images/rhino.jpg'},
];

setSlide();

arrowLeft.addEventListener('click', (e) => {
  switchSlide('left');
});

arrowRight.addEventListener('click', (e) => {
  switchSlide('right');
});

// helper functions
function getSlidePosition() {
  let pos;
  for(var i = 0; i < data.length; i++) {
    if(data[i].name === slide.getAttribute('data-slide')) {
      pos = i;
      break;
    }
  }
  return pos;
}

function setSlide() {
  const [selectedSlide] = data.filter(item => item.active === true);
  const temp = `<img class="slide__img" src=${selectedSlide.img} />`;
  //use timeout since cant do transitions just setting innerHTML
  slide.style.opacity = 0;    
    setTimeout(() => {
      slide.innerHTML = temp;
      slide.style.opacity = 1;
    }, 200)
  slide.setAttribute('data-slide', selectedSlide.name);
}

function switchSlide(direction) {
  const pos = getSlidePosition();
  switch(direction) {
    case 'left':
      if(pos === 0) {
        data[pos].active = false;
        data[data.length-1].active = true;
      } else {
        data[pos].active = false;
        data[pos-1].active = true;
      }
      setSlide();
      return;
    case 'right': 
      if(pos === data.length-1) {
        data[pos].active = false;
        data[0].active = true;
      } else {
        data[pos].active = false;
        data[pos+1].active = true;
      }
  }
  setSlide();
}


